import { newSpecPage } from '@stencil/core/testing';
import { NylasSelectedEventCard } from '../nylas-selected-event-card';

describe('nylas-selected-event-card', () => {
  it('renders correctly with initial props', async () => {
    const page = await newSpecPage({
      components: [NylasSelectedEventCard],
      html: `<nylas-selected-event-card></nylas-selected-event-card>`,
    });
    expect(page.root).toMatchSnapshot();
  });

  it('updates time display when selectedTimeslot changes', async () => {
    const page = await newSpecPage({
      components: [NylasSelectedEventCard],
      html: `<nylas-selected-event-card></nylas-selected-event-card>`,
    });

    if (!page.root) {
      throw new Error('Component did not render.');
    }

    page.root.selectedDate = new Date('2021-01-01T00:00:00Z');
    page.root.selectedTimeslot = { start_time: new Date('2021-01-01T09:00:00Z'), end_time: new Date('2021-01-01T10:00:00Z') };
    await page.waitForChanges();

    const dateDisplay = page?.root?.shadowRoot?.querySelector('[part="nsec__date"]') as HTMLDivElement;
    expect(dateDisplay.textContent).toContain('Friday, January 1, 2021');

    const timeDisplay = page?.root?.shadowRoot?.querySelector('[part="nsec__time"]') as HTMLDivElement;
    expect(timeDisplay.textContent).toContain('9:00 AM - 10:00 AM');
  });
});
